.widget {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    flex: 1;
    -webkit-box-shadow: 2px 4px 10px 1px rgba(0, 0, 0, .4);
    box-shadow: 2px 4px 10px 1px rgba(201, 201, 201, .4);
    border-radius: 10px;
    height: 100px;

    .left,
    .right {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }

    .title {
        font-weight: bold;
        font-size: 14px;
        color: rgb(160, 160, 160);
    }

    .counter {
        font-size: 28px;
        font-weight: 300;
    }

    .link {
        width: max-content;
        font-size: 12px;
        border-bottom: 1px solid lightgray;
    }

    .percentage {
        display: flex;
        align-items: center;
        font-size: 14px;

        &.positive {
            color: green;
        }

        &.negative {
            color: green;
        }
    }

    .icon {
        font-size: 28px;
        padding: 5px;
        background-color: lightblue;
        border-radius: 5px;
        align-self: flex-end;
    }
}